import uniPassword from '@/components/uni-password/uni-password.vue'
import uniIcon from '@/components/i-icon/i-icon.vue'
import uniKeyboard from '@/components/uni-keyboard.vue'
import { mapState,mapMutations } from 'vuex'
export default {
	components: {
		uniPassword,
		uniIcon,
		uniKeyboard
	},
	data() {
		return {
			tabCurrentIndex: 0,
			swiperCurrentIndex: 0,
			titleShowId: 'tabTag-0',
			tabHeight: 300,
			showKeyboard: false,
			password:'',
			psdtext:'请设置6位数的支付密码',
			UserPsdFlag:false,
			len:0,
			startX:0,
			moveEndX:0,
			valueX:0,
		}
	},
	computed: mapState([ 'UserInfo']),
	mounted() {
		var _this=this
		// _this.UserPsdFlag=true
		
		// if(this.UserInfo.userinfos.M0001_USER_PAY_PWD!=null){
		// 	// this.psdtext='请输入旧密码'
			
		// }
	},
	methods: {
		done(password) {
			console.log(password);
			console.log(this.$refs);
		},
		confirmPwd() {
			this.$refs.keyboard.show();
		},
		onInput(e) {
			//e.cancel = true;
		},
		onConfirm(e) {
			this.password = e.value;
			console.log(this.password)
		},
		tabChange(e) {
			var index = e.target.id.replace('tabTag-', '');
			this.swiperCurrentIndex = index;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		swiperChange: function(e) {
			var index = e.detail.current;
			this.tabCurrentIndex = index;
			this.titleShowId = 'tabTag-' + index;
		},
		touchstart(event){
			event.preventDefault();
	　　　　	this.startX = event.touches[0].pageX
		},
		touchmove(event){
			event.preventDefault();
			this.moveEndX = event.touches[0].pageX
			this.valueX= this.moveEndX - this.startX
			if ( this.valueX >= 80 ) {
				uni.navigateBack({
					delta:1,
					animationType: 'pop-in',
					animationDuration: 200
				})
			}
		},
	},
	onReady() {
		//获取屏幕高度及比例
		var winInfo = uni.getSystemInfo({
			success: res => {
				var windowHeight = res.windowHeight;
				//获取头部标题高度
				var dom = uni.createSelectorQuery().select('#tab-title');
				dom.fields({ size: true }, res2 => {
					if (!res2) {
						return;
					}
					//计算得出滚动区域的高度
					this.tabHeight = windowHeight - res2.height;
					console.log(this.tabHeight);
				}).exec();
			}
		});
	}
}
